%panel-tabs {
	border: none;
	padding: 0;
	width: 100%;
	display: inline-block;

	.tabs-list {
		display: inline-flex;
		flex-direction: row;
		width: 100%;
		margin: 0;
		padding: 0;

		.tab {
			display: flex;
			flex: 1;

			a {
				display: block;
				width: 100%;
				height: 100%;
				padding: 15px;
				text-align: center;
				border-right: 1px solid $gray_iron;
				color: $gray_neutral-40;

				i {
					color: $gray_neutral-80;
					display: block;
					font-size: 16px;
				}
				&:hover {
					background: $white;
					position: relative;
					i {
						color: $color_danger-light;
					}
				}
			}
			&:last-child a {
				border-right: none;
			}
			&.active a {
				background: $white;
				position: relative;
				i {
					color: $color_danger-light;
				}
			}
		}
	}
	&.panel-footer {
		.tabs-list {
			border-radius: 0 0 4px 4px;
			.tab {
				&:first-child a {
					border-radius: 0 0 0 4px;
				}
				&:last-child a {
					border-radius: 0 0 4px 0;
					border-right: none;
				}
			}
		}
	}
}
