#container {
	width: 100%;
	height: 100%;
}
.mbot30 {
	margin-bottom: 30px;
}
.inline-block {
	display: inline-block;
}
a.logo {
	font-size: 21px;
	color: $gray_neutral-20;
	float: left;
	margin-top: 15px;
	text-transform: uppercase;
	&:hover,
	&:focus {
		text-decoration: none;
		outline: none;
	}
	span {
		color: $color_danger-light;
	}
	img {
		height: 1.5em;
		vertical-align: top;
	}
}
.header,
.footer {
	min-height: 60px;
	padding: 0 15px;
}
.header {
	position: fixed;
	left: 0;
	right: 0;
	z-index: 1002;
}
.site-footer {
	background: $gray_night;
	color: $white;
	padding: 10px 0;
}
.wrapper {
	display: inline-block;
	margin-top: 60px;
	padding: 15px;
	width: 100%;
}
